<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <div class="logo_box">
          <img src="../assets/logo.png" alt="">
        </div>
        <span style='margin-left: 80px'>住房补贴系统</span>
      </div>
      <div>
        <span style='position:absolute;font-style:italic; font-size: 15px;right: 90px'>单位：{{this.dwmc}} | 用户名：{{this.username}},欢迎您！</span>
      </div>

      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 页面主体区域 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!-- 侧边栏菜单区域 -->
        <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF" unique-opened :collapse="isCollapse" :collapse-transition="false" router :default-active="activePath">
          <!-- 一级菜单 -->
          <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
            <!-- 一级菜单的模板区域 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{item.authName}}</span>
            </template>

            <!-- 二级菜单 -->
            <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveNavState('/' + subItem.path)">
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文本 -->
                <span>{{subItem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 右侧内容主体 -->
      <el-main>
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 左侧菜单数据
      menulist: [],
      iconsObj: {
        '0': 'iconfont icon-user',
        '1': 'iconfont icon-tijikongjian',
        '2': 'iconfont icon-SQLshenhe',
        '3': 'iconfont icon-danju'
      },
      // 是否折叠
      isCollapse: false,
      // 被激活的链接地址
      activePath: '',
      username: '',
      // 用户所属单位
      dwmc: ''
    }
  },
  created() {
    //this.activePath = window.sessionStorage.getItem('activePath')
    //this.menulist = this.$route.query.menulist;
    this.username= window.sessionStorage.getItem('username')
    this.dwmc= window.sessionStorage.getItem('dwmc')
    console.log(this.username)
    this.getMenuList()
    console.log(this.menulist)
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },

    // 点击按钮，切换菜单的折叠与展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 保存链接的激活状态
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    getMenuList(){
      this.$http.get('/getMenuList',
        {params:{
            username:this.username
          }},
        {emulateJSON:true}).then(res => {
        console.log(res.data)
        if (res.status !== 200) {
          return that.$message.error('获取菜单失败！')
        }
        else{
          console.log(res.data)
          this.menulist=res.data
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;

  }
}

.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.iconfont {
  margin-right: 10px;
}

.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
.logo_box {
  height: 50px;
  width: 50px;
  //border: 1px solid #eee;
  //border-radius: 10%;
  position: absolute;
  left: 10px;
  top: 5px;
  //: translate(-50%, -50%);
  background-color: #fff;
  img {
    width: 100%;
    height: 100%;
    border-radius: 10%;
    background-color: #eee;
  }
}
</style>
